@import '../../Utils/helpers';

.circle {
  border: 2px solid #fff;
  background-color: transparent;
  width: 200px;
  height: 200px;
  border-radius: 1000px; 
  margin: 0.5em;
}

.circle-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  text-align: center;
}

.text {
  font-size: 14px;
}
.time {
  font-size: 36px;
}